          if(p=="edited_by"){
            if(obj[p]=="editor"){
              edited_by = "editor"
            }else{
              edited_by = "user"
            }
          }
          if(Array.isArray(obj[p])){
            result += this.format_changed_fields_title(p) + " - " + "details have been edited\n";
          }else{
