    //function to format date in mmm yyyy format
    format_date: function (d) {
      var month = d[0]
      var year = d[1]
      switch(month) {
        case '01':
            return "Jan"+" "+year
            break;
        case '02':
            return "Feb"+" "+year
            break;
        case '03':
            return "Mar"+" "+year
            break;
        case '04':
            return "Apr"+" "+year
            break;
        case '05':
            return "May"+" "+year
            break;
        case '06':
            return "Jun"+" "+year
            break;
        case '07':
            return "Jul"+" "+year
            break;
        case '08':
            return "Aug"+" "+year
            break;
        case '09':
            return "Sep"+" "+year
            break;
        case '10':
            return "Oct"+" "+year
            break;
        case '11':
            return "Nov"+" "+year
            break;
        case '12':
            return "Dec"+" "+year
            break;
        default:
            return 0
      }
    },
