    //function to convert to pdf
    open_pdf: function () {
      //open cv html editable page in new tab using blob
      var blob = new Blob([this.cv_html], {type: 'text/html'});
      window.open(window.URL.createObjectURL(blob));
      // this.pdfbutton = false
    },
    //function to call Export2Doc function
    open_doc: function () {
      //call convert to doc function
      this.Export2Doc('exportContent','cv')
      // this.docbutton = false
    },
    //function to convert to doc format
    Export2Doc: function (element, filename = '') {
      var preHtml = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:w='urn:schemas-microsoft-com:office:word' xmlns='http://www.w3.org/TR/REC-html40'><head><meta charset='ISO-8859-1'><meta charset='utf-8'><title>Export HTML To Doc</title><style>.first-last-name{font-family: Arial; font-size: 17px;} .field-description{font-family: Arial;font-size: 14px;} .heading{font-family: Arial;font-size: 14px;}</style></head><body>"
      var postHtml = "</body></html>"
      var html = preHtml+document.getElementById(element).innerHTML+postHtml
      var blob = new Blob(['\ufeff', html], {
          type: 'application/msword'
      });
      // Specify link url
      var url = 'data:application/vnd.ms-word;charset=utf-8,' + encodeURIComponent(html)
      // Specify file name
      filename = filename?filename+'.doc':'document.doc'
      // Create download link element
      var downloadLink = document.createElement("a")
      document.body.appendChild(downloadLink)
      if(navigator.msSaveOrOpenBlob ){
          navigator.msSaveOrOpenBlob(blob, filename)
      }else{
          // Create a link to the file
          downloadLink.href = url
          // Setting the file name
          downloadLink.download = filename
          //triggering the function
          downloadLink.click()
      }
      document.body.removeChild(downloadLink)
    },
